$prime: #ff6e48;
$second: #0c1016;
$yellow: #b55400;//#fbf665
$blue: #02d7f2;
$pink:  #ff00a0;

@font-face {
    font-family: 'Cyberpunk';
    src: url('https://assets.codepen.io/907368/cyberpunk.ttf') format('truetype');
    font-weight: 200; }

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  text-align: center; }


section {
  min-height: 100%; }

a {
  text-decoration: none; }

li {
  list-style-type: none; }

h1, h2, h3, a {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  color: #494949;
  text-transform: uppercase; }

h2 {
  color: #5d7177; }

h3 {
  color: #1b1b1b;
  font-size: 40px;
  padding-top: 8%;
  margin-bottom: 5px; }

h4, p {
