hr {
  width: 30px;
  height: 3px;
  background: #1b1b1b;
  border: 0;
  margin: 0 auto 40px auto; }

#logo, li, img, .button {
  transition: all 300ms;
  -webkit-transition: all 300ms;
  -moz-transition: all 300ms;
  -o-transition: all 300ms; }


/* HOME ***************************** */
#home {
  align-items: center;
  background-color: $yellow;
  display: flex;
  font-family: 'Cyberpunk';
  font-size: 10px;
  height: 100%;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  width: 100%;
  box-sizing: border-box; }

.canvas-animation {
    position: absolute;
    z-index: 1;
    width: 100vw;
    height: 85vh;
    overflow: hidden; }

.glitch {
    z-index: 2;
    color: white;
    font-size: 10rem;
    position: relative;

    &::after {
        content: attr(data-trick);
        position: absolute;
        top: 0;
        left: 0.5rem;
        color: $yellow;
        overflow: hidden;
        text-shadow: -0.5rem 0 $pink;
        clip: rect(0, 90rem, 0, 0);
        animation: noise 2s infinite linear alternate-reverse; }

    &::before {
        content: attr(data-trick);
        position: absolute;
        top: 0;
        left: -1rem;
        color: $yellow;
        overflow: hidden;
        text-shadow: 0.3rem 0 $blue;
        clip: rect(0, 90rem, 0, 0);
        animation: noise 4s infinite linear alternate-reverse; } }

@keyframes noise {
    $steps: 25;
    @for $i from 0 through $steps {
        #{ percentage($i * (1/$steps))} {
            clip: rect(random(12)+rem, 99.99rem, random(12)+rem, 0); } } }
