/* Skills ***************************** */
#skills {
  background-color: #362624;
  align-items: center;
  //font-family: 'Roboto'
  font-size: 10px;
  justify-content: center;
  margin: 0; }

$color1: #333333;//#dedede
$color2: #333333;

.skillbars {
    width: 80%;
    position: absolute;
    left: 10%;
    height: auto;
    .skillbar {
        align-items: center;
        width: 100%;
        height: 5rem;
        border: $color1 0.1rem solid;
        border-radius: 1rem;
        position: relative;
        cursor: default;
        margin: 0 0 3rem 0;
        &.show {
            .skillbar-label {
                color: $color1; } }
        &:hover {
            .skillbar-label {
                transform: translate(0, -0.5rem);
                color: $color1; }
            .skillbar-bg {
                transform: translate(0, -0.5rem);
                box-shadow: 0 1rem 3.5rem $color1; } }
        .skillbar-label {
            width: 100%;
            height: 100%;
            padding-left: 1rem;
            position: absolute;
            top: 0;
            left: 0;
            font-size: 2rem;
            z-index: 2;
            transition: all 250ms;
            display: flex;
            //justify-content: space-between
            align-items: center;
            color: $color2; }
        .skillbar-bg {
            position: absolute;
            top: 0;
            transition: all 250ms;
            transition-timing-function: ease-in-out;
            left: 0;
            z-index: 1;
            background-color: $color2;
            border-radius: 1rem;
            width: 0;
            height: 100%; } } }



.hidden {
  visibility: hidden; }

.alert-box {
  position: fixed;
  right: 0;
  background-color: #a63002;
  font-size: 25px;
  top: 115px;
  padding: 15px 40px 15px 15px;
  color: #ffffff;
  min-width: 350px;
  z-index: 10;

  .close-alert {
    font-size: 25px;
    position: absolute;
    right: 10px;
    top: 15px;
    cursor: pointer;
    z-index: 11; }

  &.show {
    &::before {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      height: 4px;
      background-color: #d45d0e;
      width: 100%;
      box-sizing: border-box;
      z-index: 11;
      animation: hideMeter 6s linear; }

    animation: showAlert 1s ease forwards; }

  &.hide {
    animation: hideAlert 1s ease forwards; } }

@keyframes hideMeter {
  0% {
    width: 100%; }

  100% {
    width: 0%; } }

@keyframes showAlert {
  0% {
    transform: translateX(100%); }

  40% {
    transform: translateX(-15%); }

  80% {
    transform: translateX(0%); }

  100% {
    transform: translateX(-10px); } }

@keyframes hideAlert {
  0% {
    transform: translateX(-10px); }

  40% {
    transform: translateX(0%); }

  80% {
    transform: translateX(-15%); }

  100% {
    transform: translateX(100%); } }



