      background-color: #e4405f; }

    &:nth-child(2) a:hover {
      background-color: #cd201f; }

    &:nth-child(3) a:hover {
      background-color: #738ADB; }

    &:nth-child(4) a:hover {
      background-color: #cd201f; }

    &:nth-child(5) a:hover {
      background-color: #0077B5; } } }



@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');

* {
