 }  //font-family: 'Poppins', sans-serif
/* custom scroll bar */

 ::-webkit-scrollbar {
  width: 10px; }

 ::-webkit-scrollbar-track {
  background: #f1f1f1; }

 ::-webkit-scrollbar-thumb {
  background: #888; }

 ::selection {
  background: rgb(123, 255, 0.3); }

.content {
  max-width: 1250px;
  margin: auto;
  padding: 0 30px; }


.navbar {
  position: fixed;
  width: 100%;
  z-index: 3;
  padding: 25px 0;
  transition: all 0.3s ease;
  background-color: #1b1b1b;

  &.sticky {
    background: #1b1b1b;
    padding: 10px 0;
    box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.1); }

  .content {
    display: flex;
    align-items: center;
    justify-content: space-between; }

  .logo a {
    color: #fff;
    font-size: 30px;
    font-weight: 600;
    text-decoration: none; }

  .menu-list {
    display: inline-flex; } }

.menu-list li {
  list-style: none;

  a {
    color: #fff;
    font-size: 18px;
    font-weight: 500;
    margin-left: 25px;
    text-decoration: none;
    transition: all 0.3s ease;

    &:hover {
      color: #007bff; } } }



.icon {
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  display: none; }

.menu-list .cancel-btn {
  position: absolute;
  right: 30px;
  top: 20px; }



@media (max-width: 1230px) {
  .content {
    padding: 0 60px; } }

@media (max-width: 1100px) {
  .content {
    padding: 0 40px; } }

@media (max-width: 900px) {
  .content {
    padding: 0 30px; } }

@media (max-width: 868px) {
  body.disabled {
    overflow: hidden; }

  .icon {
    display: block;

    &.hide {
      display: none; } }

  .navbar {
    .menu-list {
      position: fixed;
      height: 100vh;
      width: 100%;
      max-width: 400px;
      left: -100%;
      top: 0px;
      display: block;
      padding: 40px 0;
      text-align: center;
      background: #222;
      transition: all 0.3s ease; }

    &.show .menu-list {
      left: 0%; }

    .menu-list li {
      margin-top: 45px;

      a {
        font-size: 23px;
        margin-left: -100%;
        transition: 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55); } }

    &.show .menu-list li a {
      margin-left: 0px; } }

  .glitch {
    font-size: 45px; } }

@media (max-width: 380px) {
  .navbar .logo a {
    font-size: 27px; } }









